

















































































































import { Component, Vue } from "vue-property-decorator";
import DataService from "../services/DataService";

@Component
export default class KickerList extends Vue {
  private kickers: any[] = [];
  private currentKicker: any = null;
  private currentIndex: number = -1;
  private filter: string = "";
  private amount = 10.0;
  private active = true;

  async retrieveKickers() {
    try {
      this.kickers = (
        await DataService.findWithFilter(this.active, this.filter)
      ).data;
    } catch (error) {
      console.error(error);
    }
  }

  refreshList() {
    this.retrieveKickers();
    this.currentKicker = null;
    this.currentIndex = -1;
  }

  setActiveKicker(kicker: any, index: number) {
    this.currentKicker = kicker;
    this.currentIndex = index;
  }

  async drinkBeer(id: string) {
    try {
      this.currentKicker.beersToday++;
      this.currentKicker.beersTotal++;
      await DataService.drink(id);
      this.retrieveKickers();
    } catch (error) {
      console.error(error);
    }
  }

  async undoDrinkBeer(id: string) {
    if(confirm("Letztes Bier rückgängig machen für " + id + "?")) {
      try {
        this.currentKicker.beersToday--;
        this.currentKicker.beersTotal--;
        await DataService.undoDrink(id);
        this.retrieveKickers();
      } catch (error) {
        console.error(error);
      }
    }
  }

  async payMoney(id: string, amount: number) {
    if(confirm(amount + " € einzahlen für " + id + "?")) {
      try {
        this.currentKicker.balance += amount;
        await DataService.pay(id, amount);
        this.retrieveKickers();
      } catch (error) {
        console.error(error);
      }
    }
  }

  setActive(active: boolean) {
    this.active = active;
    this.retrieveKickers();
  }

  mounted() {
    this.retrieveKickers();
  }
}
